body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Logo:hover{
   cursor: pointer;
   height: 93px;
}

.Logo{
    margin-top: -.3rem;
    margin-left: 2rem;
    height: 68px;
    transition: .5s;
}
.nav-color{
  background-color: #2b4496;

}
.nav-colors{
  background-color: #2b4496;
  height: 50px;
}
.home-icon{
    margin-top: -.5rem;
  
    height: 45px;
    transition: .5s
}
.home-icon:hover{
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
.contact-icon{
    margin-top: -.5rem;
   
    height: 45px;
    transition: .5s
}
.contact-icon:hover{
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
.money-icon{
    margin-top: -.5rem;
   
    height: 45px;
    transition: .5s
}
.money-icon:hover{
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
.call-icon{
    color: white;
    margin-left: -13rem;
    font-size: 35px;
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    
}
.number{
    color: white;
    margin-top: .5rem;
}
.btn{
margin-top: -.5rem;
}
.btn:active{
margin-top: 0rem;
}

